body {
  margin: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E5E5E5;
}

* {
  font-family: Inter;
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(54, 42, 42, 0.1);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #EAEAEA;

  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.search-block {
  border: 1px solid #F2F2F2;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear {
    position: absolute;
    height: 18px;
    width: 18px;
    top: 13px;
    right: 15px;
  }

  input {
    border: none;
    padding: 12px 30px 12px 12px;
    font-size: 16px;
    width: 200px;
  }

}





.cartTotalBlock {
  ul {
    margin-bottom: 40px !important;

    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }

    div {
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }

  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.6s ease-in-out infinite;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }

    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }

}

.greenButton {
  width: 100%;
  height: 55px;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  border: 0;
  background: #9DD558;
  border-radius: 18px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;

  &:disabled {
    background-color: rgb(180, 178, 178) !important;
    cursor: default;
  }

  &:hover {
    background: lighten(#9DD558, 10%);
  }

  &:active {
    background: darken(#9DD558, 10%);
  }
}

.cartItem {
  border: 1px solid #F2F2F2;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;

  .cartItemImg {
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }

  p {
    font-size: 16px;
    margin: 0;
  }

  b {
    font-size: 14px;
  }

  .removeBtn {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;

    &:hover {
      opacity: 1;
    }
  }
}


@keyframes button-loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.7;
  }

  100% {
    opacity: 1;
  }
}


.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .greenButton {
    width: 245px;
    margin-top: 20px;



    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }

    img {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

@media (max-width: 700px) {
  .header{
    flex-wrap: wrap;
  }
  .header__link{
    width: 100%;
    margin-bottom: 40px;
  }
  .content{
    padding: 10px !important;
  }
  .content__top{
    flex-wrap: wrap;
    margin-left: 20px;
  }
  h1.content__title{
    width: 100%;
    margin-bottom: 20px;
    font-size: 24px;
  }
}

