.card {
    border: 1px solid #e4d8d8;
    padding: 30px;
    width: 220px;
    border-radius: 40px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    margin-bottom: 30px;
    margin-right: 30px;
    .plus {
        cursor: pointer;
    }


    &:hover {
        box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.2);
        transform: translateY(-4px);
    }

    .favorite {
        position: absolute;
        cursor: pointer;
    }

    span {
        font-style: 13px;
        opacity: 0.5;
        text-transform: uppercase;
    }

    b {
        font-size: 13px;
    }

    h5 {
        font-weight: 400;
        font-size: 14px;
    }
}

@media (max-width: 700px) {
    .card{
        width: 200px;
        padding: 20px;
        margin-right: 10px;
        margin-bottom: 20px;
        text-align: center;

        .img{

        }
    }
    
}

@media (max-width: 456px) {
    .card{
        margin: 10px auto;
    }
}
